import React, { useContext, useEffect, useState } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { PlanillaContext } from "../../../context/PlanillaContext";
import Header from "../../header/Header";
import ModalPlanillaControl from "./ModalPlanillaControl";
import { controlPlanilla } from "../../../data/dataTable";
import BuscadorControlPlanilla from "../BuscadorControlPlanilla";
import useSearch from "../../../hooks/useSearch";
import TablaPlanilla from "../../tabla/TablaPlanilla";
import { Button, Empty, Input, Select } from "antd";
import { handleDownloadExcel } from "../../../helpers/tablaExcel";
import dayjs from "dayjs";

const ControlPlanilla = () => {
  const { setUserdata } = useContext(PlanillaContext);
  const { getData, setResult } = useContext(CrudContext);
  const [tableData, setTableData] = useState([]);
  const [area, setArea] = useState([]);
  const [cargo, setCargo] = useState([]);
  const [campamento, setCampamento] = useState([]);
  const [filterData, setFilterData] = useState([]);
  const [filtros, setFiltros] = useState({});
  const [planilla, setPlanilla] = useState([]);
  const [planillaControl, setPlanillaControl] = useState(false);

  const { result } = useSearch(planilla);

  const getTrabajadores = async () => {
    const route = "planilla";
    const route1 = "area";
    const route2 = "cargo";
    const route3 = "campamento";
    const response = getData(route);
    const response1 = getData(route1);
    const response2 = getData(route2);
    const response3 = getData(route3);

    const all = await Promise.all([response, response1, response2, response3]);
    setPlanilla(all[0].data);
    setArea(all[1].data);
    setCargo(all[2].data);
    setCampamento(all[3].data);
  };

  useEffect(() => {
    getTrabajadores();
  }, []);

  useEffect(() => {
    const filterData = () => {
      if (!filtros.campamento && !filtros.area && !filtros.puesto) {
        return planilla;
      }

      return planilla.filter((item) => {
        if (filtros.campamento && item?.campamento !== filtros.campamento) {
          return false;
        }

        if (filtros.area && item?.area !== filtros.area) {
          return false;
        }

        if (filtros.puesto && item?.puesto !== filtros.puesto) {
          return false;
        }

        return true;
      });
    };

    setResult(filterData());
  }, [filtros, planilla]);

  const limpiarBusqueda = () => {
    setFiltros({ campamento: undefined, area: undefined, puesto: undefined });
  };

  const handleContrato = (e) => {
    setPlanillaControl(true);
    setTableData(e);
    setUserdata(e);
  };

  const handleFilters = (e, text) => {
    setFiltros((values) => {
      return { ...values, [text]: e };
    });
  };

  const downloadExcel = () => {
    const formatData = result?.map((item, i) => {
      return {
        Nro: i + 1,
        DNI: item?.dni,
        NOMBRE: item?.nombre,
        FECHA_NACIMIENTO: item?.fecha_nacimiento,
        CELULAR: item?.telefono,
        CARGO: item?.puesto,
        ÁREA: item?.area,
        COOPERATIVA: item?.evaluacion?.at(-1)?.cooperativa,
        FECHA_DE_INGRESO: item?.fecha_inicio,
        VOLQUETE: item?.volquete,
        TELETRAN: item?.teletran,
        PERIODO_DE_TRABAJO: item?.contratos?.periodo_trabajo,
        FECHA_DE_SALIDA: item?.fecha_fin_estimada,
        ANTECEDENTES:
          item?.contratos.suspendido !== false ? "Suspendido" : "Normal",
        RECOMENDADO_POR: item?.evaluacion?.at(-1)?.recomendado_por,
        PROXIMA_QUINCENA: "",
        NUMERO_DE_QUINCENAS_TRABAJADOS:
        Math.floor(parseInt(item?.asistencia) / 15) || 0
      };
    });

    handleDownloadExcel(formatData, "Planilla", "reporte");
  };

  const columns = controlPlanilla(handleContrato);

  return (
    <div>
      <Header text={"Planilla"} user={"Usuario"} ruta={"/planilla"} />
      <div className="margenes">
        <div style={{ display: "flex", alignItems: "center", gap: "5px" }}>
          <BuscadorControlPlanilla
            registrar={false}
            crear={false}
            exportar={false}
            cargar={false}
          />
          <Select
            name="campamento"
            placeholder="Campamento"
            onChange={(e) => handleFilters(e, "campamento")}
            style={{ width: "200px" }}
            showSearch
            optionFilterProp="children"
            value={filtros.campamento}
            dropdownMatchSelectWidth={false}
            filterOption={(input, option) =>
              (option?.label ?? "").toLowerCase().includes(input.toLowerCase())
            }
            options={campamento.map((item) => {
              return { label: item.nombre, value: item.nombre };
            })}
          />
          <Select
            name="area"
            placeholder="Área"
            showSearch
            optionFilterProp="children"
            dropdownMatchSelectWidth={false}
            filterOption={(input, option) =>
              (option?.label ?? "").toLowerCase().includes(input.toLowerCase())
            }
            value={filtros.area}
            onChange={(e) => handleFilters(e, "area")}
            style={{ width: "200px" }}
            options={area.map((item) => {
              return { label: item.nombre, value: item.nombre };
            })}
          />
          <Select
            name="puesto"
            placeholder="Puesto"
            onChange={(e) => handleFilters(e, "puesto")}
            style={{ width: "200px" }}
            showSearch
            optionFilterProp="children"
            value={filtros.puesto}
            dropdownMatchSelectWidth={false}
            filterOption={(input, option) =>
              (option?.label ?? "").toLowerCase().includes(input.toLowerCase())
            }
            options={cargo.map((item) => {
              return (
                { label: "Ninguno", value: "-1" },
                { id: item.id, value: item.nombre }
              );
            })}
          />
          <Button onClick={downloadExcel}> Descargar</Button>
          <Button onClick={limpiarBusqueda}> Limpiar Busqueda</Button>
        </div>

        <TablaPlanilla
          columns={columns}
          table={result}
          handleContrato={handleContrato}
          filas={10}
        />
      </div>

      {planillaControl && (
        <ModalPlanillaControl
          selected={tableData}
          actualizarTabla={getTrabajadores}
          planillaControl={planillaControl}
          setPlanillaControl={setPlanillaControl}
        />
      )}
    </div>
  );
};

export default ControlPlanilla;
